* {
  margin: 0;
  padding: 0;
}

a {
    text-decoration: none;
    color: inherit;
}

a:hover {
    outline: none;
    border-radius: 0;
}

.loading {
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
}
.header .row {
    display: flex;
    justify-content: space-between;
}

